.hotels-list{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    max-width: 1200px;
	margin: 20px auto;
	padding: 0 5px;
}

.spinner{
	margin-top: 150px;
	display: inline-block;
	width: 100% !important;
	text-align: center;
}

.pagination-list{
	-ms-flex-preferred-size: 100%;
	    flex-basis: 100%;
	-ms-flex-positive: 0;
	    flex-grow: 0;
	-ms-flex-negative: 1;
	    flex-shrink: 1;
	margin: 10px auto;
	text-align: center;
}

.pagination {
    background-color: rgb(0, 188, 212);
	padding: 10px;
    margin: 0 10px;
    border-radius: 2px;
}

.pagination:hover{
    cursor: pointer;
    background-color: #5cc9b1be;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
}

.pagination.active{
	color: #ffffff;
}

.empty-request{
	text-align: center;
}


